@import url(https://fonts.google.com/specimen/Inter);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}body{font-family:'Inter', 'Helvetica Neue', sans-serif;background-color:#F4F4F7}

.header{max-width:1000px;margin:0 auto;padding-top:40px;margin-bottom:25px}@media (max-width: 600px){.header .tittle-space{padding-left:10px}}.header .tittle-space h4{font-weight:700;margin-bottom:0;color:#1e202a}@media (max-width: 600px){.header .tittle-space h4{font-size:17px;padding-left:15px}}.header .tittle-space h4.dark{color:#fff}.header .tittle-space p{margin-bottom:0;font-size:12px;font-weight:600}@media (max-width: 600px){.header .tittle-space p{padding-left:15px}}.header .btn-col{display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end;-webkit-align-items:center;align-items:center}.header .btn-col p{margin:0 5px 0 0;font-size:10px;font-weight:700}.header .btn-col .switchBtn{position:relative;display:inline-block;width:40px;height:20px}.header .btn-col .switchBtn input{display:none}.header .btn-col .slide{position:absolute;cursor:pointer;top:0;left:0;right:0;bottom:0;background-color:#8b97c6;transition:.4s;padding:2px;color:#fff}.header .btn-col .slide:before{position:absolute;content:"";height:16px;width:16px;left:20px;bottom:2px;background-color:#fff;transition:.4s}.header .btn-col input:checked+.slide{background:linear-gradient(90deg, #378fe6 0%, #3eda82 100%)}.header .btn-col input:focus+.slide{box-shadow:0 0 1px #00bfff}.header .btn-col input:checked+.slide:before{-webkit-transform:translateX(26px);transform:translateX(26px);left:-24px;background:#1e202a}.header .btn-col .slide.round{border-radius:34px}.header .btn-col .slide.round:before{border-radius:50%}.total-media{overflow-x:hidden;text-align:center}@media (max-width: 600px){.total-media{overflow-x:scroll;overflow-y:hidden;white-space:nowrap;width:80%;margin:0 auto;margin-bottom:30px}}h5{text-align:left;padding-left:15px;font-weight:700;margin-bottom:20px}@media (max-width: 600px){h5{padding-left:0}}.daily-media{padding-top:20px;text-align:center;max-width:1000px;margin:0 auto}@media (max-width: 600px){.daily-media{max-width:280px;max-height:500px;overflow-y:scroll}}.second-title{color:#63687e}.second-title.dark{color:#fff}

.total-bage{background-color:#f0f2fa;width:220px;height:180px;border-radius:3px;margin:15px;cursor:pointer;display:inline-block}.total-bage .total-bage__main-line{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;padding:10px}.total-bage .total-bage__main-line .network-img{width:15px;height:15px;margin-right:8px}.total-bage .total-bage__main-line .acount-txt{margin-bottom:0;font-size:11px;font-weight:700}.total-bage .total-bage__number{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.total-bage .total-bage__number .big-number{font-size:50px;margin-bottom:0;font-weight:700;color:#1e202a}.total-bage .total-bage__number .number--kind{margin-bottom:0;letter-spacing:3px;font-size:14px}.total-bage .total-bage__update{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;padding-top:10px}.total-bage .total-bage__update .update-number{font-size:10px;margin-bottom:0px;font-weight:700}.total-bage .total-bage__update .update-arrow{margin-right:5px;width:8px}.total-bage:hover{background-color:#E1E3F0}.total-bage.dark{background-color:#252a41}.total-bage.dark .big-number{color:#fff}.total-bage.dark:hover{background-color:#333A56}.total-bage.face-border,.total-bage.face-border.dark{border-top:4px solid #00bfff}.total-bage.twit-border,.total-bage.twit-border.dark{border-top:4px solid #1ca0f2}.total-bage.insta-border,.total-bage.insta-border.dark{border-bottom:0;border-left:0;border-right:0;border-style:solid;border-top-width:4px;border-image:linear-gradient(60deg, #fdc468, #df4996) 1}.total-bage.yout-border,.total-bage.yout-border.dark{border-top:4px solid #c4032a}.update-number.arrow-up,.update-number.arrow-up.dark{color:#1db489}.update-number.arrow-down,.update-number.arrow-down.dark{color:#dc414c}

.daily-badge{background-color:#f0f2fa;width:220px;height:90px;border-radius:3px;display:inline-block;margin:15px;cursor:pointer}.daily-badge .daily-badge-interaction{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;padding:12px 10px 0}.daily-badge .daily-badge-interaction .interaction-type{margin-bottom:0PX;font-size:10px;font-weight:700;padding-left:10px}.daily-badge .daily-badge-interaction .network-img{width:13px}.daily-badge .daily-badge-numbers{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;padding:10px 10px 0}.daily-badge .daily-badge-numbers .interaction-number{margin-bottom:0;font-size:30px;font-weight:700;color:#1e202a;margin-left:10px}.daily-badge .daily-badge-numbers .interaction-ammount{font-size:11px;font-weight:700;margin-bottom:0}.daily-badge .daily-badge-numbers .interaction-ammount .interaction-arrow{margin-right:5px;width:8px}.daily-badge:hover{background-color:#E1E3F0}.daily-badge.dark:hover{background-color:#333A56}.daily-badge.dark{background-color:#252a41}.daily-badge.dark .interaction-number{color:#fff}.interaction-ammount.arrow-up,.interaction-ammount.arrow-up.dark{color:#1db489}.interaction-ammount.arrow-down,.interaction-ammount.arrow-down.dark{color:#dc414c}

.overlay{z-index:10;background-color:rgba(0,0,0,0.6);position:fixed;top:0;left:0;right:0;bottom:0}.container{background:#f0f2fa;z-index:15;width:60%;height:470px;position:absolute;top:120px;left:20vw;border-radius:10px;padding-left:15px}@media (max-width: 600px){.container{width:90%;height:600px;top:150px;left:5vw}}.container .head-section{margin-top:20px;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.container .head-section h4{font-weight:bold;margin-bottom:0;color:#1e202a}.container .head-section button{color:#63687e;border:none;background:none;font-size:18px;font-weight:400;position:absolute;right:8px;top:0}.container .account-section{display:-webkit-flex;display:flex;margin-top:10px}.container .account-section .network-img{width:17px;height:17px;margin-right:8px}.container .account-section .acount-txt{margin-bottom:0;font-size:12px;font-weight:700}.container .data-section{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;margin:20px 0}.container .data-section .col-number{padding-left:0}.container .data-section .follow-number{font-size:40px;margin-bottom:0;font-weight:700;color:#63687e}.container .data-section .text-description{font-size:12px;font-weight:400;color:#63687e;margin-bottom:0;padding:0 20px 0 15px}.container .data-section .mid-txt{width:140px}.container .data-section .update-number{font-size:40px;margin-bottom:0;font-weight:700}.container .data-section .update-arrow{width:10px;margin:0 10px}.container .chart-section .date-txt{color:#63687e;font-size:16px;margin-bottom:0;font-weight:400}.container .chart-section .date-txt.dark{color:#fff}.container .chart-section .chart-cont{overflow-x:auto;margin-top:20px}.container.dark{background-color:#1e202a}.container.dark h5{color:#fff}.container.dark .follow-number{color:#fff}.container.dark .text-description{color:#fff;font-weight:300}

